.StaticContent{background:#fff;padding:10vh 0;min-height:105vh}.StaticContent span[role="quote"]:after,.StaticContent span[role="quote"]:before,.StaticContent blockquote:after,.StaticContent blockquote:before{font-family:Arial, sans-serif}.StaticContent span[role="quote"]:after,.StaticContent blockquote:after{content:close-quote}.StaticContent span[role="quote"]:before,.StaticContent blockquote:before{content:open-quote}.StaticContent span[role="quote"]:after,.StaticContent span[role="quote"]:before{font-size:1.2em;margin:0 3px}.StaticContent span[role="quote"] em{font-style:normal}.StaticContent main{border:1px solid #E3E3E3;border-left:none;border-right:none;padding:5px 0;margin:50px 0}.StaticContent main:before,.StaticContent main:after{content:"";display:block;height:1px;background:#E3E3E3}.StaticContent main blockquote{padding:60px 40px;display:block;text-align:center;color:#0078d7;font-size:1.5em;position:relative}.StaticContent main blockquote:before,.StaticContent main blockquote:after{position:absolute;font-size:2em}.StaticContent main blockquote:before{top:30px;left:30px}.StaticContent main blockquote:after{bottom:0;right:30px}.StaticContent section{width:900px;margin:0 auto}.StaticContent section h2{font-weight:100;margin:50px 0 15px}
