.Nav{position:fixed;z-index:3;top:30px;right:30px;display:flex}.Nav ul{background:#fff;border-radius:10px;list-style:none;padding:0;margin:0;box-shadow:0 10px 30px 0 rgba(20,20,20,0.08)}.Nav ul li{position:relative;border-top:1px solid #ececec}.Nav ul li:after{display:block;content:"";position:absolute;width:5px;height:5px;left:14px;top:18px;background:#00ead1;border-radius:40px;transition:1s cubic-bezier(0.8, 0.01, 0, 1.63);transform:scale(0)}.Nav ul li.selected:after{transform:scale(1.5)}.Nav ul li:first-child{border:none}.Nav ul a{display:block;text-decoration:none;padding:10px 20px 10px 30px;min-width:130px;color:#333}.Nav ul a img{opacity:.7}.Nav .btn{display:flex;justify-content:space-between}.Nav .btn a{flex:1;min-width:inherit;cursor:pointer}.Nav .btn a.disabled{opacity:.1;cursor:not-allowed}.Nav .btn a:not(.disabled):hover{opacity:.5}.Nav .btn a:nth-of-type(2){border-left:1px solid #ececec;text-align:right;padding:10px 30px 10px 20px}
